body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-header {
  text-align: center;
  background-color: white;
  min-height: 15vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #091F54;
  box-shadow: 0px 0px 15px 2px #565454;
}

.App-header img {
  max-width: 25%;
}

.App-header .Sub-header {
  font-size: 1em;
  font-weight: bold;
}

.content {
  background: #ddf4ff url(data:image/jpeg;base64,) repeat-x;
}

.code-box-demo {
  text-align: center;
}
.ant-layout-header,
.ant-layout-footer {
  background: #7dbcea;
  color: #fff;
}
.ant-layout-footer {
  line-height: 1.5;
}
.ant-layout-sider {
  background: rgba(0, 0, 0, 0);
  color: #fff;
  line-height: 120px;
}
.ant-layout-content {
  color: #fff;
  min-height: 120px;
  line-height: 120px;
}
> .code-box-demo > div > .ant-layout {
  margin-bottom: 48px;
}
> .code-box-demo > div > .ant-layout:last-child {
  margin: 0;
}
.ant-anchor-wrapper {
  padding: 0;
  background: transparent;
  margin-top: 20%;
}
.ant-anchor-ink {
  height: 0;
}
.ant-form-item {
  font-size: 20px;
}
.ant-form label {
  font-size: 20px;
}

.productrec {
  text-align: center;
  background-color: white;
}

.productrec .top {
  background-color: white;
  padding-top: 20px;
}

.productrec .mainproduct {
  width: 800px;
  height: 300px;
  margin: auto;
  margin-bottom: 20px;
  border: 2px solid lightgrey;
  position: relative;
}

.productrec .mainproduct .title {
  background-color: midnightblue;
  color: white;
  padding: 5px;
  font-size: 20px;
  font-weight: bold;
}

.productrec .rightcol {
  height: inherit;
  width: 25%;
  float: left;
}

.productrec .mainproduct .rightcol img {
  height: 225px;
  margin: auto;
  padding: 50px 0;
}

.productrec .mainproduct .leftcol h1 {
  font-size: 28px;
  font-weight: normal;
}

.productrec .mainproduct table {
  font-size: 20px;
  width: 50%;
  text-align: left;
  margin: auto;
  margin-top: 20px;
}

.productrec .product {
  background-color: white;
  color: midnightblue;
}

.productrec .alternative .product {
  display: inline-grid;
  margin: 10px;
}

.productrec tbody .tright {
  text-align: right;
}

.productrec footer {
  position: absolute;
  text-align: center;
  bottom: 0;
  width: 100%;
}

.productrec .alternative {
  background-color: midnightblue;
  width: 100%;
  margin: auto;
}

.productrec .alternative .product {
  padding-top: 10px;
  border: 2px solid lightgrey;
  width: 400px;
  height: 150px;
}

.productrec .alternative .title {
  color: white;
  padding: 5px;
  font-size: 20px;
  font-weight: bold;
  height: 100%;
}

.productrec .alternative .rightcol img {
  height: 125px;
  margin: auto;
  padding: 30px 0;
}

.productrec .alternative .leftcol h1 {
  font-size: 20px;
  font-weight: normal;
}

.productrec .alternative table {
  font-size: 16px;
  width: 60%;
  text-align: left;
  margin: auto;
  margin-top: 20px;
}

